/**
 * 
 */

module.exports = {
	data() {
		return {}
	},
	methods: {
		/**
		 * 
		 * @desc orderCancel
		 * @param {Object} config
		 */
		orderCancel(orderId, callbackFun) {
			var that = this;
			uni.showModal({
				title: '',
				content: '',
				success: function(e) {
					if (e.confirm) {
						that.$api.order.cancel.request({
							orderId: orderId
						}).then(data => {
							typeof callbackFun === 'function' && callbackFun(data)
						})
					}
				}
			});
		}
	}
}
