import indexMock from './indexMock.js'

/**
 * wx.request
 * 
 * TODO wx.request
 * TODO 
 * @param data
 * @returns {*}
 */
class requestServe {

	/**
	 * 
	 * @param {*} data 
	 */
	constructor(data) {
		this.defaultConfig = {
			apiType: 'default', //
			method: 'GET', //
			data: {}, //
			header: {}, // 
			isProcessReturnData: false, // 
			clickDelay: true, //
			showLoading: true, //loading true/false
			showLoadingMessage: '...', //
			showSuccessLoading: false, // 
			showSuccessLoadingMessage: '', // 
			showErrorLoading: true, //
			showErrorLoadingMessage: ''
		}

		this.config = {}

		// TODO 
		this.errorUrl = ''; // api,

		// api
		this.openMock = true; // ,

		/**
		 * 
		 */
		this.returnCofig = {
			codeField: 'code', //code,code,
			dataField: 'data', //
			msgField: 'msg' //
		}
		this.codeSuccess = 200 // 
		this.codeFunList = {}
		this.codeFunListSet()

		// 
		this.shakeCache = {}

		// 
		this.app = getApp();
	}

	// 
	loginCheck() {
		return true
	}

	// 
	toLogin() {
		return false;
	}

	/**
	 * [] token
	 */
	tokenCheck(config) {
		if (config.apiType == 'user') { //
			this.log(config.log + '[]');

			if (!this.loginCheck()) return this.toLogin();
		} else if (config.apiType == 'notUser') { // token
			return true
		} else if (config.apiType == 'default') {

		} else {
			this.errorAlert(config.log + 'apiType,');
			return false;
		}

		config.header['token'] = ''
		return true
	}

	/**
	 * [] code
	 */
	codeFunListSet(code, fun) {
		const codeList = {
			"301": this.toLogin
		}

		this.codeFunList = codeList
	}

	// 
	success(config) {
		let _this = this;
		return function(res) {
			if (config.isProcessReturnData === true) return true;

			if (config.showLoading == true) _this.hideLoading();

			_this.log(config.log + '[success]:', res);

			// html,200
			if (res.statusCode !== 200) {
				_this.alert(',');
				_this.errorLog(res)
				return false
			}

			//
			var content = res.data;
			var code = content[_this.returnCofig.codeField]; // code
			var data = content[_this.returnCofig.dataField] ? content[_this.returnCofig.dataField] : {}; // 
			var msg = content[_this.returnCofig.msgField] ? content[_this.returnCofig.msgField] : ''; // 

			// 
			if (code === '' || isNaN(code)) {
				//TODO 
				console.log(log + '[success][code]', code);
				_this.errorLog(res);
				_this.alert(',');
				return false;
			}

			// 
			if (_this.codeFunList[code] && typeof _this.codeFunList[code] ===
				'function') { // ,
				return _this.codeFunList[code](content)
			}

			if (code == _this.codeSuccess) { //
				if (config.showSuccessLoading == true) _this.alert(msg?.config.showSuccessLoadingMessage);
				if (typeof config.success === 'function') config.success(data, res)
			} else if (code || code === 0 || code === '0') { // ,codeError
				if (config.showErrorLoading) alert(msg?.config.showErrorLoadingMessage);
				if (typeof config.codeError === 'function') config.codeError(content, res); // codeError
			} else {
				_this.errorLog('request->success', res)
				_this.alert(',,');
			}
		}
	}

	// 
	fail(config) {
		return function(res) {
			wx.showModal({
				title: '',
				content: ',~',
				showCancel: false,
			})

			if (config.showLoading == true) this.hideLoading()
			if (typeof config.fail === 'function') config.fail(res)
		}
	}

	/**
	 * 
	 */
	request(setConfig) {
		setConfig['log'] = '' + new Date().getDate() + '-apiurl' + setConfig.api + '-->'; // log

		//,api:  -> api -> 
		var config = Object.assign({}, this.defaultConfig, setConfig);
		this.log('[api]', config)

		if (this.shakeCheck(config) === false) return false // 
		if (this.tokenCheck(config) === false) return false // token
		this.requestParamsFiltering(config) // 

		// [parameter]: 
		if (config.source) data['source'] = config.source;

		// [loading] loading
		if (config.showLoading == true) {
			wx.showLoading({
				title: config.showLoadingMessage,
				mask: true,
			})
		}

		let success = this.success(config)

		// [],
		if (this.openMock == true && config.openMock === true) return success(this.mockDataGet(config));

		// 
		var cache = wx.request({
			url: config.url,
			method: config.method,
			data: config.data,
			dataType: 'json',
			header: config.header,
			success: success,
			fail: this.fail(config),
			// complete: completeFunction
		})

		// 
		return cache;
	}

	/**
	 * loading
	 */
	hideLoading() {
		uni.hideLoading();
		this.app.globalData.isShowLoading == false;
	}

	/**
	 * 
	 * , 1
	 */
	shakeCheck(config) {
		if (config.clickDelay == true) {
			let apiName = config['api'];
			let newTime = new Date().getTime()
			if (this.shakeCache[apiName] && this.shakeCache[apiName].time - newTime > 1000) {
				this.log(config.log + apiName + ':..........');
				return false
			} else {
				this.log(config.log + apiName + ':,');
				this.shakeCache[apiName] = newTime
				return true
			}
		} else {
			return true
		}
	}

	// 
	// [parameter], TODO ,,
	requestParamsFiltering(config) {
		// [apiType]
		let params = config.data

		this.log(config.log + '[]', params);
		for (var i in params) {
			// ,
			if (typeof params[i] == 'string') params[i] = params[i].trim();

			// null TODO 
			if (params[i] === null) params[i] = '';

			//  undefined ,TODO 
			if (typeof params[i] === undefined || params[i] === undefined) params[i] = '';
		}

		this.log(config.log + '[]', params);
		return params
	}

	/**
	 * 
	 * @param {Object} api url
	 * @param {Object} params 
	 */
	mockDataGet(config) {
		let api = config.api
		let params = config.data
		let apiPath = api.split('/'); //
		let virtualData = {} // 
		let virtualReturn;

		if (apiPath.length == 1) {
			virtualReturn = indexMock[apiPath[0]]
		} else if (apiPath.length == 2) {
			virtualReturn = indexMock[apiPath[0]][apiPath[1]]
		} else if (apiPath.length == 3) {
			virtualReturn = indexMock[apiPath[0]][apiPath[1]][apiPath[2]]
		}

		// TODO data,https
		console.log('',virtualReturn)
		if (typeof virtualReturn === 'function') {
			console.log('')
			virtualData = virtualReturn(params)
		} else {
			virtualData = virtualReturn
		}

		// alert(',');
		this.log(config.log + '[]:', virtualData);
		return virtualData
	}

	/**
	 * 
	 * @param {Object} content
	 */
	errorAlert(content) {
		wx.showModal({
			title: '',
			content: content,
			showCancel: false,
		})

		return true;
	}

	// 
	errorLog(content) {
		if (this.errorUrl) {
			// TODO 
		}
	}

	// 
	alert(content) {
		if (!content) return false;

		var time = content.length / 4 * 1000;
		if (time < 2000) time = 2000;
		wx.showToast({
			title: content,
			icon: 'none',
			duration: time
		})
	}


	// 
	consoleLog(msg) {
		var time = new Date().getDate(); //
		var log = '' + time + '-apiurl' + this.api + '-->';

		console.log(log + msg)
	}

	// logo
	log(title, content) {
		console.log(title, content);
	}
}

export default requestServe
