
/**
 * describe: 
 * @param {Object} files 
 * @param {RegExp, String} reg 'underlineHead'
 *  => reg {RegExp} 
 *  => reg {String} ,['underlineHead''_XXX.js'],['letterHead''XXX.js']
 * @param {String} mode 'object'
 *  => 'object' 
 *  => 'array' 
 *  => 'single' 
 */

function moduleIn (files, mode, reg) {
  if (!files) return
  mode = mode || 'object'
  const dJsReg = {
    underlineHead: /(\_|^\.\/|\.js$)/g,
    letterHead: /(^\.\/|\.js$)/g
  }
  reg = dJsReg[reg] || reg || dJsReg.underlineHead

  var modules = mode === 'array' ? [] : {}
  var objModules = {}

  files.keys().forEach(key => {
    var mk = key.replace(reg, '')
    var m = files(key)

    objModules[mk] = Object.keys(m).reduce((s, e) => {
      if (e !== 'default') s[e] = m[e]
      if (mode === 'array') modules = modules.concat(s)
      return s
    }, m.default || {})

    if (mode === 'single') modules = Object.assign({}, modules, objModules[mk])
  })

  if (mode === 'object') {
    modules = objModules
  } else {
    objModules = null
  }

  return modules
}

export default moduleIn
