import {
	shareConfig
} from "../common/shareConfig.js"

/**
 * 
 * @desc app
 */

module.exports = {
	data() {
		return {
			share: {
				title: '', // 
				path: '', // 
				imageUrl: '' // 
			}
		}
	},
	onLoad() {

		wx.showShareMenu({
			withShareTicket: true,
			menus: ['shareAppMessage', 'shareTimeline']
		});








		// TODO ,
		this.shareSet(shareConfig)
	},

	onShareAppMessage() {
		return this.mpShare
	},


	onShareTimeline() {
		return this.mpShare
	},


	methods: {
		// ,
		shareSet(config) {
			this.share = {
				...this.share,
				...config
			}
		}
	}
}
